import React from 'react'
import styled from 'styled-components';
import search from '../image/search.png'


const Container=styled.div`
width:100px;
padding:20px;
display:flex;
flex-direction:column;
box-shadow: 7px 11px 8px 1px rgba(0,0,0,0.75) inset;
-webkit-box-shadow: 7px 11px 8px 1px rgba(0,0,0,0.75) inset;
-moz-box-shadow: 7px 11px 8px 1px rgba(0,0,0,0.75) inset;

@media only screen and(max-width:480px){
    width:50px;
}

`;

const Image=styled.img`
width:20px;
margin-left:40px;


`;
const Text=styled.span`
color:grey;
margin-top:10px;
text-align:center;

@media only screen and(max-width:480px){
   
    font-size:14px;
}
`;

const Minicard = () => {
    return (
        <Container>
            <Image src={search}/>
            <Text>
            We help our clients succeed by creating..
            </Text>
        </Container>
    )
}

export default Minicard
