import React from 'react'
import styled from 'styled-components';
import lady from '../image/student.png';
import Animinate from './Animinate';



const Container=styled.div`
height:calc(100vh - 70px);
display:flex;
padding:20px;

@media screen and (max-width:480px){
    flex-direction:column;
}

`;
const Left=styled.div`
width:60%;
display:flex;
flex-direction:column;
align-items:center;
justify-content:center;

@media screen and(max-width:480px){
    width:100%;
    height:100%;
}

`;
const Title=styled.h1`
font-size:60px;
width:60%;

@media screen and(max-width:480px){
    width:100%;
    font-size:50px;
}
`;
const Desc=styled.p`
font-size:20px;
width:60%;
@media screen and(max-width:480px){
    width:100%;
}

`;
const Info= styled.div`
width:60%;
display:flex;
justify-content:space-between;
align-items:center;

@media screen and(max-width:480px){
    flex-direction:column;

`;
const Button=styled.button`
padding:15px;
background-color:blue;
color:wheat;
border:none;
border-radius:10px;
letter-spacing:2px;

@media screen and (max-width:480px){
   margin-bottom:20px;
}

`;
const Contact=styled.div`
display:flex;
flex-direction:column;
`;
const Phone=styled.span`
color:red;
font-weight:bold;
`;
const ContactText=styled.span`
color:grey;
`;
const Right=styled.div`
width:40%;



`;
const Image=styled.img`
width:100%;
height:70%;
margin-top:50px;


`;



const Into = () => {
    return (
        <Container>
           <Left>
               <Title>
                   Adventure in creative Age
               </Title>
           <Desc>
               We beleive that designing products and service in close partnership with our client is the only way to real impact the business..
           </Desc>
           <Info>
         <Button>START A PROJECT</Button>
         <Contact>
             <Phone>Call-us (012) 345-6789</Phone>
             <ContactText>For any question Contact This Number</ContactText>
         </Contact>
           </Info>
               </Left> 
           <Right><Image src={lady}/>
           
           </Right>
          <Animinate />
        </Container>
    );
}

export default Into;
