import React from 'react'
import styled from 'styled-components';
import phone from '../image/phone1.png';
import Animinate from './Animinate';

const Container=styled.div`
 display:flex;
 justify-content:center;
 align-items:center;
 padding:30px 20px;

 @media only screen and(max-width:480px){
   flex-direction:column;
}


`;
const Left=styled.div`
 width:50%;

 @media screen and(max-width:480px){
   display:none;
}
 `;

const Image=styled.img`
margin-top:30px;
width:100%;`;


const Right=styled.div`
 width:50%;
 margin-top:60px;
 display:flex;
 flex-direction:column;
 align-items:center;


 @media screen and(max-width:480px){
    width:100%;
    
}
`;
const Title=styled.span`
font-size:70px;

@media only screen and(max-width:480px){

    font-size:50px;
}
`;

const Subtitle=styled.span`
font-size:20px;
margin-top:10px;
font-style:italic;
color:#333;
`;

const Desc=styled.p`
font-size:20px;
color:#777;
`;
const Button=styled.button`
width:150px;
border:none;
font-size:20px;
padding:15px 20px;
background-color:blue;
border-radius:12px;
color:white;

`;



const Feature = () => {
    return (
        <Container>
            <Left><Image src={phone}/></Left>
    
            <Right>
                <Title>good design<br/>
                        good business
                </Title>
                <Subtitle>We know that good design means good business</Subtitle>
                <Desc>We help our clients succeed by creating brand identies,design experience,and print material that communicate clearly,achieve marketing goals..</Desc>
                <Button>Learn-More</Button>
            </Right>
            <Animinate />
        </Container>
    )
}

export default Feature;
