import React from 'react'
import styled from 'styled-components';
import Animinate from './Animinate';


const Container=styled.div`
padding:20px;
margin-top:40px;
margin:50px;
display:flex;
flex-direction:column;
align-items:center;
background-color:white;
box-shadow: -3px 9px 14px -5px rgba(0,0,0,0.83);
-webkit-box-shadow: -3px 9px 14px -5px rgba(0,0,0,0.83);
-moz-box-shadow: -3px 9px 14px -5px rgba(0,0,0,0.83);
`;



const Pricecontainer=styled.div`
display:flex;
align-items:center;


`;
const Price=styled.span`
font-weight:bold;
font-size:50px;
`;

const Type=styled.button`
padding:10px;
margin:10px;
border:1px solid crimson;
color:crimson;
background-color:white;
border-radius:20px;
`;
const List=styled.ul`
list-style:none;


`;
const ListItem=styled.li`
margin:30px 0px;


`;
const Button=styled.button`

border:none;
background-color:darkblue;
color:white;
font-size:16px;
padding:15px;
border-radius:10px;
cursor:pointer;


`;
const Pricecard = ({price,type}) => {
    return (
        <Container>
            <Pricecontainer>
          $<Price>{price}</Price>/month
          </Pricecontainer>
        <Type>{type}plan</Type>
           <List>
           <ListItem>200 Hand-craft Templete</ListItem>
           <ListItem>Exclusive support</ListItem>
           <ListItem>50+Prebuilt website</ListItem>
           <ListItem>Premium Plugins</ListItem>

           </List>
           <Button>Join-now</Button>
           
       <Animinate />
        </Container>
    )
}

export default Pricecard;
