import React from 'react';
import styled from 'styled-components';
import map from'../image/map.png'
import telephone from '../image/telephone.png';
import mail1 from '../image/mail1.png';
import Footer from './Footer';

const Container=styled.div`
 height:90%;
 background-image:linear-gradient(180deg,red,blue);
 
`;
const Wrapper=styled.div`
 height:100%;
 padding:20px;
 display:flex;
 justify-content:center;
 align-items:center;

`;
const FormContainer=styled.div`

width:50%;
`;
const AddressContainer=styled.div`
width:50%;

`;
const Form=styled.form`
height:250px;
display:flex;
justify-content:center;
align-items:center;

`;
const LeftForm=styled.div`
height:100%;
display:flex;
flex-direction:column;
justify-content:space-between;

`;
const RightForm=styled.div`

height:100%;
display:flex;
flex-direction:column;
justify-content:space-between;

`;
const TextArea=styled.textarea`
width:200px;
height:60%;
margin-left:25px;

`;

const Input=styled.input`
width:200px;
padding:20px;

`;
const Button=styled.button`
background-color:red;
color:white;
width:150px;
border:none;
padding:15px;
margin-left:25px;
border-radius:10px;
font-weight:bold;
font-size:20px;

`;
const Title=styled.h1`
margin:50px;
text-align:center;
color:whitesmoke;
`;

const AddressItem=styled.div`
display:flex;
align-items:center;
margin-bottom:30px;
margin-left:60px;
margin-top:20px;
`;
const Icon=styled.img`
width:25px;
margin-right:20px;

`;

const Text=styled.span`
color:white;
font-size:21px;
margin-right:15px;
`;


const Contact = () => {
    return (
        <>
        <Container>
            <Wrapper>
     <FormContainer>
         <Title>Questions?<br/>Let`s get in touch</Title>
     <Form>
         <LeftForm>
   <Input placeholder="your Name"/>
   <Input placeholder="your Name"/>
   <Input placeholder="your Name"/>
   </LeftForm>
   <RightForm>
    <TextArea placeholder="your message"></TextArea>
    <Button>Send</Button>
   </RightForm>

     </Form>


     </FormContainer>
     <AddressContainer>
         <AddressItem>
             <Icon src={map}/>
             <Text>123 park Avanue st,Tamilnadu, INDIA</Text>
         </AddressItem>
         <AddressItem>
             <Icon src={telephone}/>
             <Text>345 park Avanue st,Newyork, USA</Text>
         </AddressItem>
         <AddressItem>
             <Icon src={mail1}/>
             <Text>123 park Avanue st,Manitoba, CANADA</Text>
         </AddressItem>
     </AddressContainer>


            </Wrapper>
            
        </Container>
        <Container>
             <Footer />
        </Container>
    </>
    )
}

export default Contact;
