import React, { useState } from 'react'
import styled from 'styled-components';
import lad from '../image/lad.png';
import Animinate from './Animinate';
import Minicard from './Minicard';
import Play from '../image/icon.jpg';


const Container=styled.div`
display:flex;
height:100%;

@media only screen and(max-width:480px){
   flex-direction:column;
}

`;
const Left=styled.div`
width:50%;

@media only screen and(max-width:480px){
   display:none;
}
`;
const Right=styled.div`
width:50%;
@media screen and(max-width:480px){
    width:100%;
    
}

`;
const Image=styled.img`
width:100%;
display:${(props)=>props.open && "none"};
`;
const Video=styled.video`
display:${(props)=>!props.open && "none"};
width:200px;

`;


const Wrapper=styled.div`
padding:50px;
display:flex;
flex-direction:column;

@media screen and(max-width:480px){
    padding:20px;

}

`;
const Title=styled.h1`
color:green;
font-size:40px;
`;
const Desc=styled.p`
font-size:20px;
margin-top:20px;
color:grey;
`;

const Cardcontainer=styled.div`
display:flex;
justify-content:space-between;
align-items:center;

`;
const Button=styled.button`
border:none;
width:150px;
background-color:red;
color:white;
display:flex;
justify-content:space-between;
font-size:20px;
border-radius:10px;
padding:15px 10px;
margin-top:40px;
cursor:pointer;
`;
const Icon=styled.img`
width:20px;
`;


const Services = () => {

    const[open,setOpen]=useState(false);
    return (
        <Container>
            <Left><Image  open={open}src={lad}/>
            <Video open={open}src="https://youtu.be/mZ_WfqYdRzk"/>
            </Left>

            <Right>
                <Wrapper>
                    <Title>Simple process to start</Title>
                    <Desc>We help our clients succeed by creating brand identies,design experience,and print material that communicate clearly,achieve marketing goals..We help our clients succeed by creating brand identies,design experience,and print material that communicate clearly,achieve marketing goals..</Desc>
                    <Cardcontainer>
                        <Minicard />
                        <Minicard />
                        <Minicard />
                    </Cardcontainer>
                    <Button onClick={()=>setOpen(true)}><Icon src={Play}/>How is work</Button>
                </Wrapper>
            </Right>
            <Animinate />
        </Container>
    )
}

export default Services;
